@-webkit-keyframes color {
  0%   { background: #33CCCC; }
  20%  { background: #e22fb5; }
  40%  { background: #c0866f; }
  60%  { background: #07145c; }
  80%  { background: #61046d; }
  100% { background: #33CCCC; }
  }

@keyframes color {
  0%   { background: #33CCCC; }
  20%  { background: #e22fb5; }
  40%  { background: #c0866f; }
  60%  { background: #07145c; }
  80%  { background: #61046d; }
  100% { background: #33CCCC; }
  }

body {
  background: #e6c9e9; /* Fallback */
  -webkit-animation: color 20s infinite linear;
          animation: color 20s infinite linear;
  text-align: center;
  padding: 2em;
  margin: 0;
  /* font-family: sans-serif; */
}

